import React, { useState } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import './App.css';

import Timeline from './pages/Timeline';

const App: React.FC = () =>
{
    const [fullscreenThumbnail, setFullscreenThumbnail] = useState<string | null>(null);

    return (
        <BrowserRouter>
            <Routes>
                {
                    fullscreenThumbnail
                        ?
                        (
                            <div
                                className="timeline-thumbnail-fullscreen"
                                onClick={() => setFullscreenThumbnail(null)}
                            >
                                <span />
                                <img src={fullscreenThumbnail} alt="thumbnail" />
                            </div>
                        )
                        :
                        null
                }

                <Route path="/" element={<Timeline setFullscreen={setFullscreenThumbnail} />} />
                { /* <Route path="/:id" element={<Timeline setFullscreen={setFullscreenThumbnail} />} /> */}
            </Routes>
        </BrowserRouter>
    )
}

export default App;
