import { useEffect, useState, useRef } from 'react';
import { useSearchParams } from 'react-router-dom';

import axios from 'axios';

import LoadingScreen from '../components/LoadingScreen';
import { iShareFiles } from '../types';

import sign from 'jwt-encode';

const Timeline = ({ setFullscreen }: any) =>
{
    const [searchParam] = useSearchParams();

    const [id, setId] = useState<string | null>(null)
    const [value, setValue] = useState<string | number>(0);
    const [thumbnail, setThumbnail] = useState<null | string | undefined>(null);
    const [thumbnails, setThumbnails] = useState<string[] | 'error' | 'loading'>('loading');
    const [counter, setCounter] = useState<number>(0);
    const count = useRef(0);

    const [password, setPassword] = useState<string>("");
    const [modalVisible, setModalVisible] = useState<boolean>(true);

    const getBase64 = async (url: string) => 
    {
        try {
            const result = await axios
            .get(url, {
                responseType: 'arraybuffer'
            })
            return URL.createObjectURL(new Blob([result.data]))
        } catch (error) {
            console.log(error);
        }
    };

    useEffect(() => {
        if (searchParam.get('id'))
            setId(searchParam.get('id'))
        else
            setThumbnails('error')
    }, [searchParam])

    useEffect(() => {
        setPassword(document.cookie || "");
        setModalVisible(true);
    }, [id]);

    function base64() {
        if(Array.isArray(thumbnails)){
            thumbnails.forEach( async (url: string, index: number) => {
                if(url.includes("http")){
                    const base64 = await getBase64(url);
                    if(base64){
                        setThumbnails((prev: any) => {
                            if (Array.isArray(prev)){
                                const newArray = [...prev];
                                newArray[index] = base64;
                                return newArray;
                            }else{
                                return prev;
                            }
                        });
                    }
                }
            });
        }
    }
    
    useEffect(() => {
        if(counter !== thumbnails.length){
            base64();
        }
    // eslint-disable-next-line
    }, [counter]);

    useEffect(() => {
        if(Array.isArray(thumbnails)){
            count.current = thumbnails.filter((x : string) => x.includes("blob:")).length + 1;
        }
    }, [thumbnails]);

    const changeValue = async (value: string | number) =>
    {
        if(count.current > thumbnails.length){
            count.current = count.current - 1;
        }
        setValue(value);
        setThumbnail(thumbnails[Number(value)]);
    };

    const handleEnter = () => {
        (async () => {
            setModalVisible(false);
            if (id === "") return;

            const data = {
                'webview': true,
                'password': password,
                'time': new Date()
            };
            const jwt = sign(data, 'JoeyTheDog');
            
            const response = await axios.get<iShareFiles>('/getShareFiles', {
                params: { id, jwt },
                baseURL: 'https://api.buildcohesion.com',
            });

            console.log(response.data);

            if (response.data.success && response.data.pictures.length) {
                console.log('success');
                document.cookie = password;
                setThumbnails(response.data.pictures);
                setThumbnail(response.data.pictures[0]);
                setValue(0);
                setCounter(counter+1);
            } else {
                setThumbnails('error');
            }
        })();
    }

    return modalVisible
        ? <div className='w-[100vw] h-[100vh] flex items-center justify-center bg-white shadow-md'>
            <div className="max-w-xl rounded overflow-hidden shadow-lg border">
                <div className="px-6 py-4">
                    <div className="font-bold text-xl mb-2">Password is Required</div>
                    <p className="text-gray-700 text-base">To access this link, you should input password.</p>
                </div>
                <div className="px-6 mb-6">
                    <input 
                        className="shadow appearance-none border border-red-500 rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" 
                        type="password" placeholder="******************"
                        value={password}
                        onChange={(event) => setPassword(event?.target.value || "")}
                    />
                    <button 
                        className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" 
                        type="button" onClick={() => handleEnter()}
                    >
                        Enter
                    </button>
                </div>
            </div>
        </div>
        : <div className="timeline-container flex flex-1 items-center justify-center py-10">
            {
                thumbnails === 'loading'
                    ?
                    <LoadingScreen loadingError={null}/>
                    :
                    null
            }
            {
                thumbnails === 'error'
                    ?
                    <p className="m-auto mb-3rem w-50 font-semibold text-2xl">
                        This link is invalid.
                    </p>
                    :
                    null
                
            }
            {
                Array.isArray(thumbnails)
                    ?
                    <>
                        <div
                            className="timeline-thumbnail"
                            onClick={() => setFullscreen(thumbnail || null)}
                        >
                            {thumbnail === undefined ? (
                                <span className="bg-gray-300" />
                            ) : thumbnail ? (
                                <img src={thumbnail} alt="thumbnail" className='aspect-auto h-full'/>
                            ) : (
                                <LoadingScreen loadingError={null} />
                            )}
                        </div>
                        <div className="timeline">
                            {thumbnails?.length > 0 ? (
                                <>
                                    <ul className="timeline-items">
                                        <li className='timeline-item'
                                            style={{ width: `${count.current / thumbnails.length * 100}%`, backgroundColor: 'rgb(17 24 39)', position: 'absolute', zIndex : -1, left:0, maxWidth: '100%'}}
                                        >
                                        
                                        </li>
                                    </ul>
                                    <input
                                        className="timeline-track"
                                        title="range"
                                        type="range"
                                        step="1"
                                        min="0"
                                        max={thumbnails.length - 1}
                                        value={value}
                                        onChange={(event) => changeValue(event?.target?.value || 0)}
                                    />
                                </>
                            ) : null}
                        </div>
                        <p className='p-0 m-0'>{count.current}/{thumbnails.length}</p>
                    </>
                    :
                    null   
            }         
        </div>;
};


export default Timeline;



